body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(250, 250, 250);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  width: 88%;
  margin: auto;
  text-align: center;
}

.restaurants {
  border-radius: 20px;
  width: 300px;
  margin-top: 40px;
  background-color: rgb(255, 255, 255);
  padding-bottom: 20px;
}

.restaurants p {
  color: grey;
  margin-top: -10px;
  font-weight: 600;
}

.restaurants h3 {
  color: black;
  margin-bottom: 30px;
  font-size: larger;
  font-weight: 800;
}
.restaurants .price {
  color: rgb(255, 71, 44);
}

.home .delete .button {
  border: solid 1px red;
  background-color: rgb(255, 71, 44);
  color: white;
}

.Nav h3 {
  background-color: rgb(255, 70, 45);
  color: white;
  width: 70px;
  border-radius: 50px;
  text-align: center;
  margin-left: 50px;
}

.Nav Link {
  border: solid 1px red;
}

.Nav {
  border: solid 1px grey;
  background-color: rgb(295, 295, 295);
}

.search {
  padding-top: 4px;
  border: solid 1px grey;
}

.Nav Button {
  margin-top: 19px;
  margin-left: 5px;
}

.Formatted {
  border: solid 1px grey;
  display: grid;
  grid-template-columns: 3.2fr 1fr;
}

.Formatted h3 {
  background-color: rgb(255, 70, 45);
  color: white;
  width: 70px;
  border-radius: 50px;
  text-align: center;
  margin-left: 50px;
}

.Formatted Button {
  font-weight: 500;
  font-size: 0.875rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-;
  margin-top: 10px;
}

.Form-Container {
  padding: 30px;
}

.Form-Container form {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  display: grid;
  grid-template-columns: 50% 50%;
  width: 48%;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 20px;
  padding: 30px;
}

.Form-Container form .TextField {
  border: solid 1px whitesmoke;
  border-radius: 15px;
  width: 300px;
}

.Form-Container .Form-Input {
  /* border: solid 2px red; */
  margin: auto;
  margin-top: 15px;
}

.Form-Container .Form-Button {
  display: grid;
  width: 200%;
  margin-top: 40px;
}

.Form-Container .Form-Button button {
  background-color: rgb(255, 71, 44);
  font-weight: 800;
  width: 97%;
  margin: auto;
}

.Form-Container form .Phone {
  border: solid 1px rgb(192, 190, 190);
  border-radius: 5px;
  width: 300px;
  height: 56px;
}

.Form-Container form .Time {
  border: solid 1px rgb(192, 190, 190);
  border-radius: 5px;
  width: 300px;
  height: 56px;
}

.Form-Container .Form-Time {
  padding-left: 15px;
  margin-top: 15px;
  height: 80px;
}

.Form-Container form .Menu {
  border: solid 1px whitesmoke;
  border-radius: 5px;
  width: 300px;
}

.Form-Input .error {
  margin-top: -2px;
  font-size: small;
}

.restaurant {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}

.Venue {
  width: 85%;
  margin-top: 30px;
  padding-left: 30px;
}

.Info {
  border: solid 1px #eaeaea;
  width: 90%;
  background-color: rgb(255, 255, 255);
  margin-top: 30px;
  border-radius: 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.separator {
  background: #919191;
  border-radius: 50%;
  height: 5px;
  margin: 0 10px;
  width: 5px;
}

.Container-Cuisine-Price {
  align-items: center;
  display: flex;
}

.Venue .Venue-Title {
  font-family: Beatrice, GT America, Helvetica, Verdana, sans-serif;
  font-size: 44px;
  letter-spacing: -1px;
  line-height: 50px;
  margin-bottom: 0px;
}

.Container-Cuisine-Price .Cuisine,
.Container-Cuisine-Price .Price {
  color: #737373;
  font-family: GT America, Helvetica, Verdana, sans-serif;
  font-size: 16px;
  letter-spacing: -0.25px;
  line-height: 24px;
}

.Venue .Description {
  box-sizing: border-box;
}

.Info-Title {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding-top: 30px;
  margin-bottom: 50px;
}

.Location,
.Contact,
.Hours,
.Info-Cuisine,
.Info-Price,
.Info-Link {
  display: flex;
  border: solid 1px #eaeaea;
  height: 45px;
  align-items: center;
  display: flex;
}
.Location h4,
.Location p {
  padding-left: 10px;
}

.Contact h4,
.Contact p {
  padding-left: 10px;
}

.Hours h4,
.Hours .Open {
  padding-left: 10px;
}
.Info-Cuisine h4,
.Info-Cuisine p {
  padding-left: 10px;
}
.Info-Price h4,
.Info-Price p,
.Info-Link {
  padding-left: 10px;
}

.new-reservation-container {
  background-color: rgb(250, 250, 250);
}

.new-reservation {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 5px;
  width: 500px;
  padding: 20px;
  margin-left: 10px;
}

.new-reservation .reservation {
  width: 210px;
}

.new-reservation-container .time {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;

  width: 520px;
  padding: 10px;
  margin-top: 5px;
  margin-left: 10px;
  margin-bottom: -10px;
}

.new-reservation-container .Button {
  margin: 4px;
}

.new-reservation-container .TextField {
  width: 230px;
  margin-top: 15px;
}

.new-reservation-container .guests {
  width: 230px;
  margin-top: -3px;
}

.new-reservation-container .phone {
  border: solid 1px rgb(183, 179, 179);
  width: 230px;
  height: 53px;
  margin-top: 16px;
  margin-bottom: 30px;
  border-radius: 3px;
}

.reservation .error {
  margin-top: -10px;
  font-size: small;
}

.new-reservation-container .date {
  margin-top: -2px;
}

.new-reservation-container .form-button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  margin-bottom: 30px;
  padding: 15px;
}

.new-reservation-container .form-button .button {
  border: solid 2px grey;
  width: 530px;
  background-color: rgb(255, 255, 255);
  color: black;
  margin: auto;
}

.res-container {
  height: 665px;
  background-color: rgb(250, 250, 250);
}

.res-home {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  background-color: rgb(250, 250, 250);
  width: 75%;
  margin: auto;
  text-align: center;
  margin-top: 40px;
}

.res {
  border-radius: 20px;
  width: 200px;
  margin-top: 40px;
  background-color: rgb(255, 255, 255);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px;
}

.res-container .res-home .res h3,
.res-container .res-home .res p {
  color: black;
}

.res-container .res-home .no_reservation {
  margin-left: 0px;
  text-align: center;
  margin-top: 200px;
  width: 1080px;
}

.reservation-container {
  background-color: rgb(245, 245, 245);
  height: 700px;
}

.reservation-container .content h2 {
  border: solid 1px grey;
  display: grid;
  width: 300px;
  margin: auto;
  text-align: center;
  border-radius: 10px;
  margin-top: 40px;
  margin-bottom: 30px;
}

.reservation-container .content {
  border: solid 1px whitesmoke;
  width: 40%;
  margin: auto;
  border-radius: 10px;
  padding: 40px;
  background-color: rgb(255, 255, 255);
  margin-top: 60px;
  margin-bottom: 60px;
}

.reservation-container .content .contact,
.reservation-container .content .email,
.reservation-container .content .guests,
.reservation-container .content .date,
.reservation-container .content .time {
  display: grid;
  grid-template-columns: 85px 10px;
}

.reservation-container .content .name {
  display: grid;
  grid-template-columns: 70px 60px 50px;
  box-sizing: content-box;
}

.reservation-container .content .contact,
.reservation-container .content .guests,
.reservation-container .content .time {
  height: 40px;
  margin-top: -20px;
}

.reservation-container .content .name p,
.reservation-container .content .email p,
.reservation-container .content .guests p,
.reservation-container .content .date p,
.reservation-container .content .time p {
  border: solid 1px red;
  width: -webkit-max-content;
  width: max-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 22px;
}

.reservation-container .content .contact p {
  border: solid 1px red;
  width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 22px;
}

